function updateText() {
    let i = 0;
    let state = 'main';

    return function _updateText() {
        const textHTML = document.getElementById("text");

        if (textData[state].length > i) {
            textHTML.innerHTML += textData[state][i];
            i += 1;
        }

        else {

            if (state == 'main') {
                let data = prompt('e ai?')
                if (data == undefined) {
                    return;
                }
                else if (data.toLowerCase()[0] == 's') {
                    state = 'sim';
                    textHTML.innerHTML = "";
                    i = 0;
                }
                else if (data.toLowerCase()[0] == 'n') {
                    state = 'nao';
                    textHTML.innerHTML = "";
                    i = 0;
                }
            }
            else if (state == 'sim') {
            } else if (state == 'nao') {
                state = 'main';
                textHTML.innerHTML = "";
                i = 0;
            }


        };

        setTimeout(_updateText, 100);
    }
}


let app = {
    async initScene() {

        setTimeout(updateText(), 1000);

        const para = document.createElement("code");
        para.setAttribute('id', 'text');
        para.style.color = 'white';
        para.style.fontWeight = 'bold';
        para.style.background = 'black';
        para.style.fontSize = '32px';
        para.style.position = 'absolute 0px 0px';
        const node = document.createTextNode("");
        para.appendChild(node);
        const element = document.getElementById("container");
        document.body.insertBefore(para, element);
        document.body.style.background = 'black';


        this.controls = new OrbitControls(camera, renderer.domElement)
        this.controls.enableDamping = true

        // Scene setup taken from https://threejs.org/examples/#webgl_lights_rectarealight
        // Create rect area lights
        RectAreaLightUniformsLib.init()

        let rectLight1 = new THREE.RectAreaLight(0xffffa0, 5, 4, 10)
        rectLight1.position.set(- 5, 5, 5)
        //scene.add(rectLight1)

        //scene.add(new RectAreaLightHelper(rectLight1))

        // Create the floor
        const geoFloor = new THREE.BoxGeometry(200, 0.1, 200)
        const matStdFloor = new THREE.MeshStandardMaterial({ color: 0x808080, roughness: 0.5, metalness: 0 })
        const mshStdFloor = new THREE.Mesh(geoFloor, matStdFloor)
        // need await to make sure animation starts only after texture is loaded
        // this works because the animation code is 'then-chained' after initScene(), see core-utils.runApp
        await this.loadTexture(mshStdFloor)
        //scene.add(mshStdFloor)
        scene.add(raymarcher);

        // Create the torus knot
        const geoKnot = new THREE.TorusKnotGeometry(1.5, 0.5, 200, 16)
        const matKnot = new THREE.MeshStandardMaterial({ color: 0xffffff, roughness: 0, metalness: 0 })
        // save mesh to 'this' so that we can access it in the 'updateScene' function
        this.meshKnot = new THREE.Mesh(geoKnot, matKnot)
        this.meshKnot.position.set(0, 5, 0)
        // update orbit controls to target meshKnot at center
        this.controls.target.copy(raymarcher.position)



    },
    loadTexture(mshStdFloor) {
        return new Promise((resolve, reject) => {
            var loader = new THREE.TextureLoader()
            loader.load(Tile, function(texture) {
                texture.wrapS = THREE.RepeatWrapping
                texture.wrapT = THREE.RepeatWrapping
                texture.repeat.set(40, 40)
                mshStdFloor.material.map = texture
                resolve()
            }, undefined, function(error) {
                console.log(error)
                reject(error)
            })
        })
    },
    // @param {number} interval - time elapsed between 2 frames
    // @param {number} elapsed - total time elapsed since app start
    updateScene(interval, elapsed) {
        this.controls.update()
        let date = new Date();
        //raymarcher.material.uniforms.time.value = date.getMilliseconds();
        //raymarcher.time = millis;
        //raymarcher.blending = millis;

        //console.log(raymarcher.material.uniforms.time);

        // rotate the torus
        this.meshKnot.rotation.y = elapsed * params.speed
    }
}

/**************************************************
 * 3. Run the app
 * 'runApp' will do most of the boilerplate setup code for you:
 * e.g. HTML container, window resize listener, mouse move/touch listener for shader uniforms, THREE.Clock() for animation
 * Executing this line puts everything together and runs the app
 * ps. if you don't use custom shaders, pass undefined to the 'uniforms'(2nd-last) param
 * ps. if you don't use post-processing, pass undefined to the 'composer'(last) param
 *************************************************/
runApp(app, scene, renderer, camera, true, raymarcher.uniforms, composer)
